import {
  CCard,
  CCardTitle,
  CDataTable,
  CButton,
  CModal,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CInput,
  CAlert,
} from "@coreui/react";
import React, { useEffect, useState } from "react";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import axios from "axios";
import { useSelector } from "react-redux";
import { selectUser } from "../../localyStore";
import { Add } from "@material-ui/icons";
const server = process.env.REACT_APP_SERVER;
const Shipping = () => {
  const [loading, setLoading] = useState(true);
  const user = useSelector(selectUser);
  const [address, setAddress] = useState("");
  const [back, setBack] = useState("");
  const [reload, setReload] = useState(false);
  const [addModal, setAddModal] = useState(false);
  const [shippingChargeModal, setShippingChargeModal] = useState(false);
  const [newAddressName, setNewAddressName] = useState("");
  const [newAddressId, setNewAddressId] = useState("");
  const [isCOD, setIsCOD] = useState(true);
  const [isInside, setInsideSide] = useState(true);

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  //
  const [shippingChargerData, setShippingChargerData] = useState({
    insideCharge: "",
    outsideCharge: "",
    minWeightRange: "",
    maxWeightRange: "",
    isActive: true,
  });

  //shipping method
  const [feild, setFeild] = useState(null);

  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_SHIPPING_ADDRESS_GET, {
        headers: {
          Authorization: `Bearer ${user?.token}`,
        },
      })
      .then((res) => {
        setAddress(res.data.addresses);
        setBack(res.data.addresses);
        setLoading(false);
      })
      .catch((err) => {
        setLoading(false);
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "error on server",
        });
        clearEvent(5000);
      });
  }, [reload, user]);

  // get address loctions
  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_SHIPPING_CHARGE, {
        headers: {
          Authorization: `Bearer ${user?.token}`,
        },
      })
      .then((res) => {
        setFeild(res.data.shippingCharge);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "error on server",
        });
        clearEvent(5000);
      });
  }, [reload, user]);

  //delete  api
  const deleteAddress = (id) => {
    axios
      .delete(
        server + process.env.REACT_APP_ADMIN_SHIPPING_ADDRESS_DELETE + id,
        {
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setNewAddressName("");
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  const backAddress = () => {
    setNewAddressId("");
    setAddress(back);
  };
  //add new address for shiping
  const addAddress = () => {
    axios
      .post(
        server + process.env.REACT_APP_ADMIN_SHIPPING_ADDRESS_POST,
        {
          name: newAddressName,
          parentId: newAddressId,
          isCODAvailable: isCOD,
          isValleyExists: isInside,
        },
        {
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setAddModal(false);
        setNewAddressName("");
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  // toggleshipiingCharge
  const toggleshipiingCharge = (item) => {
    setShippingChargeModal(!shippingChargeModal);
    if (item?._id) {
      setShippingChargerData(item);
    }
  };
  // shipping charger add / edit
  const addShippingCharge = () => {
    axios
      .post(
        server + process.env.REACT_APP_ADMIN_SHIPPING_CHARGE_POST,
        { ...shippingChargerData },
        {
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setShippingChargerData({
          minRangePrice: 0,
          maxRangePrice: 0,
          minWeightRange: 0,
          maxWeightRange: 0,
          active: true,
        });
        setShippingChargeModal(false);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  // shipping charge delete
  const deleteShippingcharger = (id) => {
    console.log(id);
    axios
      .delete(
        server + process.env.REACT_APP_ADMIN_SHIPPING_CHARGE_DELETE + id,
        {
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  return (
    <div>
      <CCard className="p-4">
        <div className="d-flex align-items-center justify-content-between">
          <CCardTitle>Shipping Price : </CCardTitle>
          <CButton color="success" onClick={toggleshipiingCharge}>
            Add New
          </CButton>
        </div>
        <hr />
        <div className="d-flex justify-content-start">
          <div className="d-flex" style={{ marginRight: "28%" }}>
            <h5>Product Weight</h5>
          </div>
          <h5 style={{ marginRight: "7%" }}>InRange price</h5>
          <h5 style={{ marginRight: "5%" }}>OutRange price </h5>
          <h5>Active</h5>
        </div>
        <hr />
        {feild?.map((item) => (
          <div
            key={item.id}
            className="d-flex justify-content-between align-items-center my-2"
          >
            <div className="d-flex align-items-center">
              <input
                style={{
                  height: "30px",
                  weight: "10px",
                }}
                placeholder="Kg"
                name="to"
                value={item.minWeightRange}
                disabled
              />
              <span className="mx-2">To</span>
              <input
                style={{
                  height: "30px",
                  weight: "10px",
                }}
                placeholder="Kg"
                name="from"
                value={item.maxWeightRange}
                disabled
              />
            </div>
            <input
              style={{
                height: "30px",
                weight: "40px",
              }}
              placeholder="Price"
              name="inRange"
              value={item.insideCharge}
              disabled
            />
            <input
              style={{
                height: "30px",
                weight: "40px",
              }}
              placeholder="Price"
              name="outRange"
              value={item.outsideCharge}
              disabled
            />
            <input
              style={{
                height: "30px",
                weight: "40px",
              }}
              type="checkbox"
              placeholder="Kg"
              name="Active"
              checked={item.isActive}
              disabled
            />
            <CButton
              className="d-flex align-items-center"
              variant="outline"
              color="success"
              onClick={() => toggleshipiingCharge(item)}
            >
              <NoteAddIcon /> Edit
            </CButton>
            <CButton
              className="d-flex align-items-center"
              variant="outline"
              color="danger"
              onClick={() => deleteShippingcharger(item._id)}
            >
              <DeleteForeverIcon />
              <span>Delete</span>
            </CButton>
          </div>
        ))}
        {/* <br />
        <CButton
          className="w-25"
          color="success"
          onClick={() => setFeild(feild + 1)}
          disabled
        >
          Save
        </CButton> */}
        <hr />
        {/* end.. */}
        {/* {for address only } */}
        <CCardTitle>Shipping Address : </CCardTitle>
        <div>
          <CButton
            style={{ float: "left", borderColor: "#d9d9d9" }}
            onClick={backAddress}
            disabled={!newAddressId}
          >
            Back
          </CButton>
          <CButton
            className="d-flex justify-content-center align-items-center"
            style={{ float: "right" }}
            variant="outline"
            color="success"
            onClick={() => setAddModal(!addModal)}
          >
            <Add />
            Add New
          </CButton>
        </div>
        <hr />
        <CDataTable
          tableFilter
          loading={loading}
          items={address}
          fields={[
            { key: "SiNo.", _style: { width: "15%" } },
            { key: "name", _style: { width: "30%" } },
            { key: "a", _style: { width: "25%", opacity: "0" } },
            { key: "Edit", label: "", _style: { width: "10%" } },
            { key: "Delete", label: "", _style: { width: "10%" } },
          ]}
          hover
          sorter
          outlined
          scopedSlots={{
            "SiNo.": (item, index) => <td key={index}>{index + 1}</td>,
            a: (item, index) => <td></td>,
            Edit: (item, index) => (
              <td key={index}>
                <CButton
                  className="d-flex align-items-center"
                  variant="outline"
                  color="success"
                  onClick={() => {
                    setAddress(item?.children);
                    setNewAddressId(item._id);
                  }}
                >
                  <NoteAddIcon /> Edit
                </CButton>
              </td>
            ),
            Delete: (item, index) => (
              <td key={index}>
                <CButton
                  className="d-flex align-items-center"
                  variant="outline"
                  color="danger"
                  onClick={() => deleteAddress(item._id)}
                >
                  <DeleteForeverIcon />
                  <span>Delete</span>
                </CButton>
              </td>
            ),
          }}
        />
        <hr />
        <CModal
          className="sm-30"
          show={addModal}
          onClose={() => setAddModal(false)}
        >
          <CModalHeader closeButton>COMFIRM</CModalHeader>
          <CModalBody>
            <label>Name</label>
            <CInput
              type="text"
              name="address"
              onChange={(e) => setNewAddressName(e.target.value)}
              placeholder="name"
              value={newAddressName}
            />
            <br />
            <div className="d-flex align-items-center">
              <input
                style={{ cursor: "pointer" }}
                type="checkbox"
                checked={isInside}
                name="inRange"
                id="checkboxShipping"
                onChange={(e) => setInsideSide(e.target.checkedn)}
              />
              <div className="d-flex align-items-center justify-content-center">
                <label
                  className="ml-2 mt-3"
                  htmlFor="checkboxShipping"
                  style={{ cursor: "pointer" }}
                >
                  <h5>InRange</h5>
                </label>
              </div>
            </div>
            <div className="d-flex align-items-center">
              <input
                style={{ cursor: "pointer" }}
                type="checkbox"
                name="isCod"
                checked={isCOD}
                id="checkboxShippingInrange"
                onChange={(e) => setIsCOD(e.target.checkedn)}
              />
              <div className="d-flex align-items-center justify-content-center">
                <label
                  className="ml-2 mt-3"
                  htmlFor="checkboxShippingInrange"
                  style={{ cursor: "pointer" }}
                >
                  <h5>COD available</h5>
                </label>
              </div>
            </div>
          </CModalBody>
          <CModalFooter>
            <CButton onClick={() => setAddModal(false)} color="primary">
              Cancel
            </CButton>
            <CButton onClick={addAddress} color="primary">
              Add
            </CButton>
          </CModalFooter>
        </CModal>
        {/* shippingCharge */}
        <CModal
          className="sm-30"
          show={shippingChargeModal}
          onClose={toggleshipiingCharge}
        >
          <CModalHeader closeButton>COMFIRM</CModalHeader>
          <CModalBody>
            <h5>Weight Range</h5>
            <div className="d-flex align-items-center">
              <input
                style={{
                  height: "30px",
                  weight: "10px",
                }}
                min={0}
                type="number"
                placeholder="Kg"
                name="minWeightRange"
                value={shippingChargerData.minWeightRange}
                onChange={(e) =>
                  setShippingChargerData((old) => ({
                    ...old,
                    minWeightRange: e.target.value,
                  }))
                }
              />
              <span className="mx-2">To</span>
              <input
                style={{
                  height: "30px",
                  weight: "10px",
                }}
                min={0}
                type="number"
                placeholder="Kg"
                name="maxWeightRange"
                value={shippingChargerData.maxWeightRange}
                onChange={(e) =>
                  setShippingChargerData((old) => ({
                    ...old,
                    maxWeightRange: e.target.value,
                  }))
                }
              />
            </div>
            <h5 className="mt-4 mb-2">Inside Charge</h5>
            <input
              style={{
                height: "30px",
                weight: "10px",
              }}
              min={0}
              type="number"
              placeholder="RS."
              name="minRangePrice"
              value={shippingChargerData.insideCharge}
              onChange={(e) =>
                setShippingChargerData((old) => ({
                  ...old,
                  insideCharge: e.target.value,
                }))
              }
            />
            <br />
            <h5 className="mt-4 mb-2">Outside Charge</h5>
            <input
              style={{
                height: "30px",
                weight: "10px",
              }}
              min={0}
              type="number"
              placeholder="RS."
              name="maxRangePrice"
              value={shippingChargerData.outsideCharge}
              onChange={(e) =>
                setShippingChargerData((old) => ({
                  ...old,
                  outsideCharge: e.target.value,
                }))
              }
            />
            <div className="d-flex align-items-center mt-4">
              <p className="mr-2">IsActive:</p>
              <p className="mt-2">
                <input
                  type="checkbox"
                  name="active"
                  checked={shippingChargerData.isActive}
                  onChange={(e) =>
                    setShippingChargerData((old) => ({
                      ...old,
                      isActive: e.target.checked,
                    }))
                  }
                />
              </p>
            </div>
          </CModalBody>
          <CModalFooter>
            <CButton onClick={toggleshipiingCharge} color="primary">
              Cancel
            </CButton>
            <CButton onClick={addShippingCharge} color="primary">
              Add
            </CButton>
          </CModalFooter>
        </CModal>
      </CCard>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};
export default Shipping;
